<template lang="pug">
    .login
        .login__content
            form(@submit.prevent="login").login__form
                .login__form-title Авторизация
                label.login__row Введите ИИН
                    .form-input__wrapper-login
                        input(
                            v-model="user.username"
                            required
                        ).input__iin
                label.login__row Введите пароль
                    .form-input__wrapper-password
                        input(
                            type="password"
                            v-model="user.password"
                            required
                        ).input__password
                .login__btn
                button(
                    type="submit"
                ).login__send-data Войти
</template>

<script>
import axios from "axios";
import requests from "../../requests";
export default {
  data: () => ({
    user: {
      username: "",
      password: ""
    }
  }),
  methods: {
    async login() {
      try {
        const response = await axios.post("http://b53895e0110a.ngrok.io/auth/login",  this.user); 
        const token = response.data.token;
        localStorage.setItem("token", token);
        requests.defaults.headers["Authorization"] = `Bearer_${token}`;
        this.$router.replace("/");
      } catch (error) {
        console.log(error.data)
      }
    }
  }
};
</script>
